<template>
  <div v-click-away="onClickAway">
    <transition name="popup">
      <div
        v-show="open"
        class="
          fixed
          flex flex-col
          z-50
          bottom-24
          top-0
          right-0
          h-auto
          sm:top-auto sm:right-5 sm:left-auto
          w-96
          overflow-auto
          border border-gray-200 dark:border-gray-800
          shadow-2xl
          rounded-md
        "
      >
        <Contact />
      </div>
    </transition>
    <div>
      <button
        @click="openMessage"
        :class="{ '-translate-x-16': translate }"
        class="
          fixed
          hidden
          z-40
          right-5
          bottom-5
          transform
          shadow-lg
          md:flex
          justify-center
          items-center
          w-14
          h-14
          bg-indigo-400
          rounded-full
          hover:bg-indigo-500
          focus:bg-indigo-600
          transition
          duration-300
          ease
          dark:bg-indigo-600
          dark:hover:bg-indigo-700
          dark:focus:bg-indigo-800
        "
      >
        <transition name="button">
          <svg
            class="w-6 h-6 text-white absolute"
            v-show="!open"
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            stroke-width="2"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path
              d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"
            ></path>
          </svg>
        </transition>
        <transition name="close">
          <svg
            class="w-6 h-6 text-white"
            v-show="open"
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            stroke-width="2"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <line x1="18" y1="6" x2="6" y2="18"></line>
            <line x1="6" y1="6" x2="18" y2="18"></line>
          </svg>
        </transition>
      </button>
    </div>
  </div>
</template>

<script>
import Contact from "./Contact.vue";

export default {
  components: {
    Contact,
  },
  data() {
    return {
      translate: false,
    };
  },
  computed: {
    open() {
      return this.$store.state.openPopup
    }
  },
  methods: {
    scrollListener: function () {
      window.scrollY > 89 ? (this.translate = true) : (this.translate = false);
    },
    onClickAway() {
      if(this.$store.state.toBeClosed){
        this.$store.commit('close')
      }
    },
    openMessage(){
      !this.$store.getters.openPopup && this.emitter.emit("toggle-contactPopup", this.$store.getters.openPopup)
      if(window.innerWidth > 1024){
        this.$store.commit('toggle')
      } else{
        this.$router.push({name: 'contact'})
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.scrollListener);
    window.addEventListener("keyup", (e) => {
      if(e.key == 'Escape'){
        this.$store.commit('close')
      }
    });
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.scrollListener);
  },
};
</script>

<style scoped>
/* form popup */
.popup-enter-from{
  opacity: 0;
  transform: translateY(5px);
}
.popup-enter-active,
.popup-leave-active{
  transition: all 0.5s ease;
}

.popup-leave-to{
  opacity: 0;
  transform: translateY(5px);
}

/* button */
.button-enter-from{
  opacity: 0;
  transform: rotate(-45deg) scale(.75);
}
.button-leave-to{
  opacity: 0;
  transform: rotate(-45deg);
}
.button-enter-active{
  transition: all .2s ease .1s;
}
.button-leave-active{
  transition: all .1s ease;
}

/* close */
.close-enter-from{
  opacity: 0;
  transform: rotate(45deg) scale(.75);
}
.close-leave-to{
  opacity: 0;
  transform: rotate(45deg);
}
.close-enter-active{
  transition: all .2s ease .1s;
}
.close-leave-active{
  transition: all .1s ease;
}
</style>
