<template>
  <a
    @click="scrollTop"
    v-show="visible"
    class="
      shadow-lg
      fixed
      bottom-20
      md:bottom-5
      right-2
      md:right-5
      bg-indigo-400
      cursor-pointer
      w-14
      h-14
      flex
      justify-center
      items-center
      rounded-full
      transition-all
      duration-300
      ease-out
      text-white
      z-40
      hover:bg-indigo-500
      dark:bg-indigo-600
      dark:hover:bg-indigo-700
    "
  >
    <svg
      xmlns="http://www.w3.org/2000/svg"
      class="h-6 w-6"
      fill="none"
      viewBox="0 0 24 24"
      stroke="currentColor"
    >
      <path
        stroke-linecap="round"
        stroke-linejoin="round"
        stroke-width="2"
        d="M5 10l7-7m0 0l7 7m-7-7v18"
      />
    </svg>
  </a>
</template>

<script>
export default {
  data() {
    return {
      visible: false,
    };
  },
  methods: {
    scrollTop: function () {
      this.intervalId = setInterval(() => {
        if (window.pageYOffset === 0) {
          clearInterval(this.intervalId);
        }
        window.scroll(0, window.pageYOffset - 50);
      }, 10);
    },
    scrollListener: function () {
      this.visible = window.scrollY > 89;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrollListener);
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.scrollListener);
  },
};
</script>

