<template>
  <metainfo>
    <template v-slot:title="{ content }">{{ content ? `${content} | firmanmimang` : `firmanmimang` }}</template>
  </metainfo>
  <div class="antialiased font-mono dark:bg-gray-800 min-h-screen flex flex-col">
    <navbar />
    <div class="flex-grow pt-16 md:pt-0">
      <transition name="route" mode="out-in">
        <router-view v-slot="{Component}">
          <component :is="Component"></component>
        </router-view>
      </transition>
    </div>
    <footer-component class="mt-auto" />
    <scroll-top/>
    <ContactPopup />
    <bottom-bar />
    <vue-progress-bar />
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import FooterComponent from "@/components/Footer.vue";
import ScrollTop from "@/components/ScrollTop.vue";
import ContactPopup from "@/components/ContactPopup.vue";
import BottomBar from "@/components/MobileBottomBar.vue";
import { useMeta } from 'vue-meta'

export default {
  components: {
    Navbar,
    FooterComponent,
    ScrollTop,
    ContactPopup,
    BottomBar,
  },
  setup () {
    useMeta({
      title: 'Portfolio',
      htmlAttrs: { lang: 'en', amp: true }
    })
  },
  mounted() {
    //  [App.vue specific] When App.vue is finish loading finish the progress bar
    this.$Progress.finish();
  },
  created() {
    //  [App.vue specific] When App.vue is first loaded start the progress bar
    this.$Progress.start();
    //  hook the progress bar to start before we move router-view
    this.$router.beforeEach((to) => {
      //  does the page we want to go to have a meta.progress object
      if (to.meta.progress !== undefined) {
        let meta = to.meta.progress;
        // parse meta tags
        this.$Progress.parseMeta(meta);
      }
      //  start the progress bar
      this.$Progress.start();
      //  continue to next page
      // next();
    });
    //  hook the progress bar to finish after we've finished moving router-view
    this.$router.afterEach(() => {
      //  finish the progress bar
      this.$Progress.finish();
    });
  },
};
</script>

<style>
/* MOUSE STYLING */
/* width */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #fff;
}

/* Handle */
::-webkit-scrollbar-thumb {
  --tw-bg-opacity: 1;
  background: rgba(229, 231, 295, var(--tw-bg-opacity));
  /* border-radius: 100vh; */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(209, 213, 219, 1);
}

html.dark ::-webkit-scrollbar-thumb{
  background: rgba(55, 65, 81, 1);
  /* border-radius: 100vh; */
}

html.dark ::-webkit-scrollbar-track{
  background: #1f2937;
}

html.dark ::-webkit-scrollbar-thumb:hover{
  background: rgba(75, 85, 99, 1);
}

/* TRANSITION ROUTER */
/* route transition */
.route-enter-from {
  opacity: 0;
}
.route-enter-to{
  opacity: 1;
}
.route-enter-active{
  transition: all 0.3s ease-out;
}

.route-leave-from{
  opacity: 1;
}
.route-leave-to{
  opacity: 0;
}
.route-leave-active{
  transition: all 0.3s ease-out;
}
</style>