<template>
  <nav v-click-away="closeNavbar" class="bg-white z-50 fixed md:static w-full block md:flex items-center justify-between border-b dark:bg-gray-800 dark:border-gray-700">
    <div class="flex w-full items-center justify-between px-6 py-4 relative z-50 bg-white dark:bg-gray-800">
      <div class="flex items-center">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 dark:text-gray-100" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
        </svg>
        <button 
            title="Toggle Theme" 
            @click="darkMode"
            class=" mx-1 w-12  h-6  rounded-full  p-1  bg-gray-800  dark:bg-gray-200  relative  transition-colors  duration-300  ease-in focus:outline-none  focus:ring-0 focus:ring-blue-700  dark:focus:ring-blue-600  focus:border-transparent">
              <div id="toggle" class="rounded-full  w-4  h-4   bg-white dark:bg-indigo-500 relative  pointer-events-none  transition-all  duration-300  ease-out">
              </div>
        </button>
        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 dark:text-gray-300" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
        </svg>
      </div>
      <div>
        <ul class="hidden md:flex items-center space-x-8 font-normal text-gray-700 dark:text-white">
          <li><router-link :to="{name: 'home'}" class=" hover:text-gray-500 dark:hover:text-gray-200 transition duration-200">Home</router-link></li>
          <li><router-link :to="{name: 'project'}" class=" hover:text-gray-500 dark:hover:text-gray-200 transition duration-200">Pojects</router-link></li>
          <li>
            <a href="mailto:fhidayat131@gmail.com" class=" m-2 px-4 py-2 rounded transition-color duration-200 bg-indigo-400 hover:bg-indigo-500 focus:bg-indigo-600 flex dark:bg-indigo-600 dark:hover:bg-indigo-700 dark:focus:bg-indigo-800">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-gray-200" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
              </svg>
              <div class="text-white">
                &nbsp; Mail me
              </div>
            </a>
          </li>
        </ul>
        <div class="md:hidden" @click="open = !open" >
          <transition name="switch" mode="out-in">
              <svg v-if="!open" xmlns="http://www.w3.org/2000/svg" class="h-7 w-7 text-gray-700 dark:text-white cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16"/>
              </svg>
              <svg class="h-7 w-7 text-red-500 cursor-pointer" v-else xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                <line x1="18" y1="6" x2="6" y2="18"></line>
                <line x1="6" y1="6" x2="18" y2="18"></line>
              </svg>
          </transition>
        </div>
      </div>
    </div>

    <transition name="openmenu">
      <div v-if="open" class="md:hidden w-full border-b dark:border-gray-700 absolute top-12 z-10 bg-white dark:bg-gray-800">
        <ul class="px-5 font-normal text-gray-700 dark:text-white">
          <li class="pt-2"><router-link :to="{name: 'home'}" class=" hover:text-gray-500 dark:hover:text-gray-200 transition duration-200">Home</router-link></li>
          <li class="pt-2"><router-link :to="{name: 'project'}" class=" hover:text-gray-500 dark:hover:text-gray-200 transition duration-200">Pojects</router-link></li>
          <li class="pt-2 pb-4">
            <a href="mailto:fhidayat131@gmail.com" class="">
              <div class="flex px-4 py-2 justify-center rounded transition-color duration-200 bg-indigo-400 hover:bg-indigo-500 focus:bg-indigo-600 dark:bg-indigo-600 dark:hover:bg-indigo-700 dark:focus:bg-indigo-800">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-gray-200" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                </svg>
                <div class="text-white">
                  &nbsp;Mail me
                </div>
              </div>
            </a>
          </li>
        </ul>
      </div>
    </transition>
  </nav>
</template>

<script>
export default {
    data() {
      return {
        dark: false,
        open: false,
      }
    },
    methods: {
      darkMode(){
        localStorage.getItem('theme') == 'dark' ? this.dark=true : this.dark=false
        this.dark = !this.dark
        const mode = this.dark ? 'dark' : 'light'
        localStorage.setItem('theme', mode)

        const html = document.querySelector('html')
        const slideToggle = document.getElementById('toggle')
        html.classList.add(localStorage.getItem('theme'))
        mode == 'dark' ? html.classList.remove('light') : html.classList.remove('dark')
        localStorage.getItem('theme') == 'dark' ? slideToggle.classList.add('ml-6') : slideToggle.classList.remove('ml-6')
        this.$store.commit('darkMode', localStorage.getItem('theme'))
      },
      closeNavbar(){
        this.open = false
      }
    },
    mounted(){
      document.querySelector('html').classList.add(localStorage.getItem('theme'))
      const slideToggle = document.getElementById('toggle')
      localStorage.getItem('theme') == 'dark' ? slideToggle.classList.add('ml-6') : slideToggle.classList.remove('ml-6')
      this.$store.commit('darkMode', localStorage.getItem('theme'))
    },
};
</script>

<style>
  /* switch trigger */
  .switch-enter-from,
  .switch-leave-to {
    opacity: 0;
    transform: scale(.9);
  }
  .switch-enter-active,
  .switch-leave-active {
    transition: all 0.2s ease-in;
  }

  /* openmenu */
  .openmenu-enter-from,
  .openmenu-leave-to {
    opacity: 0;
    transform: translateY(-150px);
  }
  .openmenu-enter-active,
  .openmenu-leave-active {
    transition: all 0.4s ease-out;
  }
</style>