<template>
  <div class="bg-gray-50 flex-grow p-6 dark:bg-gray-700">
    <form @submit.prevent="sendForm" novalidate>
      <input type="checkbox" name="botcheck" id="" style="display: none" />

      <div class="mb-4">
        <label
          for="full_name"
          class="block mb-2 text-sm text-gray-600 dark:text-gray-400"
          >Name<sup class=" text-red-500">*</sup></label
        >
        <input
          v-model="form.name"
          type="text"
          name="name"
          id="full_name"
          placeholder="Your Name"
          required
          class="
            w-full
            px-3
            py-2
            bg-white
            placeholder-gray-300
            border border-gray-300
            rounded-md
            focus:outline-none
            focus:ring-1
            focus:ring-indigo-100
            focus:border-indigo-300
            dark:bg-gray-800
            dark:border-gray-700
            dark:placeholder-gray-600
            dark:text-gray-200
          "
        />
      </div>

      <div class="mb-4">
        <label
          for="email"
          class="block mb-2 text-sm text-gray-600 dark:text-gray-400"
          >Email Address<sup class=" text-red-500">*</sup></label
        >
        <input
          v-model="form.email"
          type="email"
          name="email"
          id="email"
          placeholder="you@company.com"
          required
          class="
            w-full
            px-3
            py-2
            bg-white
            placeholder-gray-300
            border border-gray-300
            rounded-md
            focus:outline-none
            focus:ring-1
            focus:ring-indigo-100
            focus:border-indigo-300
            dark:bg-gray-800
            dark:border-gray-700
            dark:placeholder-gray-600
            dark:text-gray-200
          "
        />
      </div>

      <div class="mb-4">
        <label
          for="message"
          class="block mb-2 text-sm text-gray-600 dark:text-gray-400"
          >Your Message<sup class=" text-red-500">*</sup></label
        >

        <textarea
          v-model="form.message"
          rows="4"
          name="message"
          id="message"
          placeholder="I would love to hear from you, so whether you need a quote, have a question or just want to say hello, please feel free to drop me a line."
          class="
            w-full
            h-32
            px-3
            py-2
            bg-white
            placeholder-gray-300
            border border-gray-300
            rounded-md
            focus:outline-none
            focus:ring-1
            focus:ring-indigo-100
            focus:border-indigo-300
            dark:bg-gray-800
            dark:border-gray-700
            dark:placeholder-gray-600
            dark:text-gray-200
          "
          required
        ></textarea>
      </div>
      <div class="mb-3">
        <button
          type="submit"
          class="
            w-full
            px-3
            py-4
            text-white
            bg-indigo-500
            rounded-md
            transtition duration-200
            hover:bg-indigo-600
            focus:bg-indigo-700 focus:outline-none
            
          "
        >
          <template v-if="!loadingState"> Send Message </template>
          <template v-if="loadingState">
            <svg
              class="h-6 w-6"
              xmlns="http://www.w3.org/2000/svg"
              xmlns:xlink="http://www.w3.org/1999/xlink"
              style="
                margin: auto;
                background: none;
                display: block;
                shape-rendering: auto;
              "
              viewBox="0 0 100 100"
              preserveAspectRatio="xMidYMid"
            >
              <g>
                <circle cx="60" cy="50" r="4" fill="#a5b4fc">
                  <animate
                    attributeName="cx"
                    repeatCount="indefinite"
                    dur="1.0204081632653061s"
                    values="95;35"
                    keyTimes="0;1"
                    begin="-0.6566000000000001s"
                  ></animate>
                  <animate
                    attributeName="fill-opacity"
                    repeatCount="indefinite"
                    dur="1.0204081632653061s"
                    values="0;1;1"
                    keyTimes="0;0.2;1"
                    begin="-0.6566000000000001s"
                  ></animate>
                </circle>
                <circle cx="60" cy="50" r="4" fill="#a5b4fc">
                  <animate
                    attributeName="cx"
                    repeatCount="indefinite"
                    dur="1.0204081632653061s"
                    values="95;35"
                    keyTimes="0;1"
                    begin="-0.3234s"
                  ></animate>
                  <animate
                    attributeName="fill-opacity"
                    repeatCount="indefinite"
                    dur="1.0204081632653061s"
                    values="0;1;1"
                    keyTimes="0;0.2;1"
                    begin="-0.3234s"
                  ></animate>
                </circle>
                <circle cx="60" cy="50" r="4" fill="#a5b4fc">
                  <animate
                    attributeName="cx"
                    repeatCount="indefinite"
                    dur="1.0204081632653061s"
                    values="95;35"
                    keyTimes="0;1"
                    begin="0s"
                  ></animate>
                  <animate
                    attributeName="fill-opacity"
                    repeatCount="indefinite"
                    dur="1.0204081632653061s"
                    values="0;1;1"
                    keyTimes="0;0.2;1"
                    begin="0s"
                  ></animate>
                </circle>
              </g>
              <g transform="translate(-15 0)">
                <path
                  d="M50 50L20 50A30 30 0 0 0 80 50Z"
                  fill="#e0e7ff"
                  transform="rotate(90 50 50)"
                ></path>
                <path d="M50 50L20 50A30 30 0 0 0 80 50Z" fill="#e0e7ff">
                  <animateTransform
                    attributeName="transform"
                    type="rotate"
                    repeatCount="indefinite"
                    dur="1.0204081632653061s"
                    values="0 50 50;45 50 50;0 50 50"
                    keyTimes="0;0.5;1"
                  ></animateTransform>
                </path>
                <path d="M50 50L20 50A30 30 0 0 1 80 50Z" fill="#e0e7ff">
                  <animateTransform
                    attributeName="transform"
                    type="rotate"
                    repeatCount="indefinite"
                    dur="1.0204081632653061s"
                    values="0 50 50;-45 50 50;0 50 50"
                    keyTimes="0;0.5;1"
                  ></animateTransform>
                </path>
              </g>
            </svg>
          </template>
        </button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        apikey: process.env.VUE_APP_WEB3_FORM,
        subject: "Contact Form Question",
        from_name: "PortfolioKu",
        botcheck: false,
        name: "",
        email: "",
        message: "",
      },
      loadingState: false,
    };
  },
  methods: {
    async sendForm() {
      const regexEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      if (!this.form.name || !this.form.email || !this.form.message) {
        this.$toast.open({
          message: "all fields marked with * are required",
          type: "error",
          duration: 3000,
          queue: true,
          dismissible: true,
        });
        return false;
      }
      if(this.form.email.search(regexEmail) == -1){
        this.$toast.open({
          message: "your email address is not valid",
          type: "error",
          duration: 3000,
          queue: true,
          dismissible: true,
        });
        return false;
      }
      this.loadingState = true;
      this.$Progress.start()
      let json = JSON.stringify(this.form);
      await fetch("https://api.web3forms.com/submit", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: json,
      })
        .then(async (response) => {
          let json = await response.json();
          if (response.status === 200) {
            this.$toast.open({
              message: "Your message sent succesfully",
              type: "success",
              duration: 3000,
              queue: true,
              dismissible: true,
            });
            this.form.name = "";
            this.form.email = "";
            this.form.phone = "";
            this.form.message = "";
            this.$Progress.finish()
          } else {
            this.$toast.open({
              message: json.message,
              type: "error",
              duration: 3000,
              queue: true,
              dismissible: true,
            });
            this.$Progress.fail()
          }
          this.loadingState = false;
        })
        .catch(() => {
          this.$toast.open({
            message: "Something went wrong",
            type: "error",
            duration: 3000,
            queue: true,
            dismissible: true,
          });
          this.loadingState = false;
          this.$Progress.fail()
        });
    },
  },
  mounted() { 
    this.emitter.on("toggle-contactPopup", () => {
      this.$nextTick(()=>{
        setTimeout(function() {
          document.getElementById("full_name")?.focus()
        }, 10);
      })
    });
  },
};
</script>